.gameBoard {
  display: grid;
  font-family: serif;
  font-style: italic;
  grid-template-columns: auto auto auto;
  height: 100vh;
  text-align: center;
  width: 100vw;
}
.gameOver {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}
.preGame {
  -ms-flex-line-pack: center;
      align-content: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}
.preGame button {
  background: #ddd;
  border-radius: 100px;
  border: none;
  cursor: pointer;
  font-size: 24px;
  height: 50px;
  margin: 0 15px;
  width: 50px;
}
.gameOver a {
  cursor: pointer;
  text-decoration: underline;
}
.label {
  display: block;
  font-size: 48px;
}
.position {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  font-size: 100px;
  -ms-flex-pack: center;
      justify-content: center;
}
button {
  background-color: #EEE;
  border: 1px solid #DDD;
}
.position, button {
  height: calc(100vh / 3);
  width: calc(100vw / 3);
}
.gameBoard :nth-child(1),
.gameBoard :nth-child(4),
.gameBoard :nth-child(7) {
  border-left: 0;
}
.gameBoard :nth-child(1),
.gameBoard :nth-child(2),
.gameBoard :nth-child(3) {
  border-top: 0;
}
.gameBoard :nth-child(3),
.gameBoard :nth-child(6),
.gameBoard :nth-child(9) {
  border-right: 0;
}
.gameBoard :nth-child(7),
.gameBoard :nth-child(8),
.gameBoard :nth-child(9) {
  border-bottom: 0;
}


